import React, {Component} from 'react';import ReactDOM from 'react-dom';import {Link} from 'react-router-dom';import axios from 'axios';class App extends Component {    constructor(props) {        super(props);        this.state = {            books: []        };    }    componentDidMount() {        axios.get('/books')            .then(res => {                this.setState({books: res.data});                // console.log("App",                // this.state.books);            });    }    render() {        return (            <div class="container">                <div class="panel panel-default">                    <div class="panel-heading">                        <h3 class="panel-title">                            Список книг                        </h3>                    </div>                    <div class="panel-body">                        <h4><Link to="/create"><span class="glyphicon glyphicon-plus-sign"                                                     aria-hidden="true"></span> Добавить книгу</Link></h4>                        <table class="table table-stripe">                            <thead>                            <tr>                                <th>Название</th>                                <th>Описание</th>                                <th>Автор</th>                                <th>Жанр</th>                            </tr>                            </thead>                            <tbody>                            {this.state.books.map(c =>                                <tr>                                    <td>{c.name}</td>                                    <td>{c.comment}</td>                                    <td>{c.author}</td>                                    <td>{c.genre}</td>                                    <td><Link to={`/show/${c.id}`}>Подробно</Link></td>                                </tr>                            )}                            </tbody>                        </table>                    </div>                </div>            </div>        );    }}export default App;